<template>
  <!-- 列表 infinite-list-wrapper -->
  <div class="list">
    <div class="list-ul" v-infinite-scroll="load">
      <div class="use-no-height" v-if="name==='units'">
        <listX :onItem="item" v-for="(item, i) in count" :key="i"></listX>
      </div>
      <div v-else>
        <listY :onItem="item" v-for="(item, i) in jurysData" :key="i"></listY>
      </div>
    </div>
    <p v-if="loading" style="text-align: center">加载中...</p>
    <p v-if="noMore" style="text-align: center">没有更多了</p>
  </div>
</template>
<script>
import listY from '@/components/list/listY.vue'
import listX from '@/components/list/listX.vue'
import { mapState } from 'vuex'
export default {
  data () {
    return {
      count: [
        {
          id: 1,
          image_url:
            'http://1251517655.vod2.myqcloud.com/05255768vodcq1251517655/3e677cf15285890794134088614/5285890794134088615.jpg',
          name: '大渝网',
          type: '主办单位',
          url: 'http://www.dayuw.cn'
        },
        {
          id: 1,
          image_url:
            'http://1251517655.vod2.myqcloud.com/05255768vodcq1251517655/3e677cf15285890794134088614/5285890794134088615.jpg',
          name: '大渝网',
          type: '主办单位',
          url: 'http://www.dayuw.cn'
        },
        {
          id: 1,
          image_url:
            'http://1251517655.vod2.myqcloud.com/05255768vodcq1251517655/3e677cf15285890794134088614/5285890794134088615.jpg',
          name: '大渝网',
          type: '主办单位',
          url: 'http://www.dayuw.cn'
        },
        {
          id: 1,
          image_url:
            'http://1251517655.vod2.myqcloud.com/05255768vodcq1251517655/3e677cf15285890794134088614/5285890794134088615.jpg',
          name: '大渝网',
          type: '主办单位',
          url: 'http://www.dayuw.cn'
        },
        {
          id: 1,
          image_url:
            'http://1251517655.vod2.myqcloud.com/05255768vodcq1251517655/3e677cf15285890794134088614/5285890794134088615.jpg',
          name: '大渝网',
          type: '主办单位',
          url: 'http://www.dayuw.cn'
        },
        {
          id: 1,
          image_url:
            'http://1251517655.vod2.myqcloud.com/05255768vodcq1251517655/3e677cf15285890794134088614/5285890794134088615.jpg',
          name: '大渝网',
          type: '主办单位',
          url: 'http://www.dayuw.cn'
        },
        {
          id: 1,
          image_url:
            'http://1251517655.vod2.myqcloud.com/05255768vodcq1251517655/3e677cf15285890794134088614/5285890794134088615.jpg',
          name: '大渝网',
          type: '主办单位',
          url: 'http://www.dayuw.cn'
        },
        {
          id: 1,
          image_url:
            'http://1251517655.vod2.myqcloud.com/05255768vodcq1251517655/3e677cf15285890794134088614/5285890794134088615.jpg',
          name: '大渝网',
          type: '主办单位',
          url: 'http://www.dayuw.cn'
        }
      ],
      loading: false,
      name: '',
      jurysData: []
    }
  },
  components: {
    listX,
    listY
  },
  created () {
    let name = this.$route.query.name
    this.name = name
  },
  computed: {
    noMore () {
      return this.count >= 200
    },
    disabled () {
      return this.loading || this.noMore
    },
    ...mapState({
      units: state => state.admin.gameIntroduction.units,
      judges: state => state.admin.gameIntroduction.judges
    })
  },
  methods: {
    load () {
      this.loading = true
      if (this.name === 'units') {
        setTimeout(() => {
          this.count = this.count.concat(this.units)
          this.loading = false
        }, 3000)
      } else {
        setTimeout(() => {
          this.jurysData = this.jurysData.concat(this.judges)
          this.loading = false
        }, 3000)
      }
    }
  }
}
</script>
<style lang='less'>
.list {
  width: 100%;
  box-sizing: border-box;
  padding: 18px;
  height: 70vh;
  .list-ul {
    height: 100%;
    overflow: auto;
    .use-no-height {
      margin-bottom: 20px;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      .itemempty {
        height: 0px;
        width: 32%;
      }
    }
  }
}
.infinite-list {
  height: 70vh;
}
</style>



