<template>
  <!-- x轴排列  v-for="item in units"-->
  <a :href="onItem.url" style="text-decoration: none;margin: 10px 0;">
    <div class="unit-item">
      <div class="pro-box">
        <el-image :src="onItem.image_url" class="img" fit="cover"></el-image>
        <div class="name">{{onItem.name}}</div>
      </div>
      <div class="unit">{{onItem.type}}</div>
    </div>
  </a>
</template>
<script>
export default {
  props: {
    onItem: {
      type: [Object, Number, String],
      default () {
        return {}
      }
    }
  }
}
</script>
<style lang='less' scoped>
.unit-item {
  width: 100px;
  // height: 120px;
  // margin: 10px 0;
  // margin-top: 12px;
  border: solid 1px #e5e5e5;
  .pro-box {
    position: relative;
    width: 100%;
    height: 100px;
    .img {
      width: 100%;
      height: 100%;
    }
    .name {
      position: absolute;
      width: 100%;
      background-color: #000000;
      // opacity: 0.5;
      opacity: 0.75;
      left: 0;
      bottom: 0;
      font-size: 12px;
      color: #ffffff;
      padding: 7px 0 5px 0;
      text-align: center;
      letter-spacing: 1px;
    }
  }
  .unit {
    width: 100%;
    padding: 5px 0;
    font-size: 12px;
    color: #fe741d;
    text-align: center;
    letter-spacing: 1px;
  }
}
</style>



