<template>
  <!-- Y轴排列 v-for="item in judges" -->
  <div class="jury-item" @click="juryDescribe(onItem.id)">
    <div class="jury-img-box">
      <el-image :src="onItem.avatar" class="img" fit="cover"></el-image>
    </div>
    <div class="jury-info-box">
      <div class="name">{{onItem.name}}</div>
      <div class="summary">{{onItem.desc}}</div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    onItem: {
      type: [Object],
      default () {
        return {}
      }
    }
  },
  methods: {
    juryDescribe (id) {
      this.$router.push({ path: '/option/juryRe', query: { id } })
    }
  }
}
</script>
<style lang='less' scoped>
.jury-item {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  box-sizing: border-box;
  padding: 10px;
  border: solid 1px #e5e5e5;
  margin: 15px 0;
  .jury-img-box {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 12px;
    .img {
      width: 100%;
      height: 100%;
    }
  }
  .jury-info-box {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: 1;
    .name {
      font-size: 14px;
      color: #333333;
      margin-bottom: 10px;
    }
    .summary {
      font-size: 12px;
      color: #333333;
      text-overflow: ellipsis;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
    }
  }
}
</style>



